import React from "react";

import HackerRank from "./hacker-rank";
import Instagram from "./instagram";
import Email from "./email";
import WhatsApp from "./whatsapp";
import GitHub from "./github";
import LinkedIn from "./linkedin";

const Icon = (props) => {
  switch (props.name) {
    case "instagram":
      return <Instagram {...props} />;
    case "hackerrank":
      return <HackerRank {...props} />;
    case "email":
      return <Email {...props} />;
    case "whatsapp":
      return <WhatsApp {...props} />;
    case "github":
      return <GitHub {...props} />;
    case "linkedin":
      return <LinkedIn {...props} />;
    default:
      return <div></div>;
  }
};

export default Icon;
